<template>
  <div class="cell-row">
    <div class="row-wrap">
      <div
        class="cell-label"
        :class="[labelFixTop ? 'top-label' : '']"
        :style="{ width: labelWidth + 'px' }"
      >
        {{ label }}
      </div>
      <div v-if="showValue" class="cell-value">
        <slot />
      </div>
      <div class="cell-action">
        <slot name="action" />
      </div>
    </div>
    <div v-if="showLine" class="underline"></div>
  </div>
</template>

<script>
export default {
  name: "CellRow",
  props: {
    label: {
      type: String,
      default: "",
    },
    labelWidth: {
      type: Number,
      default: 70,
    },
    showValue: {
      type: Boolean,
      default: true,
    },
    action: {
      type: Boolean,
      default: false,
    },
    labelFixTop: {
      type: Boolean,
      default: false,
    },
    showLine: {
      type: Boolean,
      default: true,
    },
  },
};
</script>
