.row-wrap ::v-deep {
  display: flex;
  align-items: center;
  padding: 24px 30px;
  .cell-label {
    font-size: 14px;
    font-weight: 400;
    color: #333333;
    &.top-label {
      align-self: flex-start;
    }
  }
  .cell-value {
    font-size: 14px;
    font-weight: 400;
    color: #666666;
    margin-right: 20px;
  }
  .cell-action {
  }
  .el-button {
    padding: 0;
    border: none;
  }
}
.underline {
  height: 1px;
  background-color: #f2f2f2;
}
</style>
